<template>
  <div id="box">
    <div class="content">
        <div class="section">
            <div class="item comment">
                <h3>{{title}}</h3>
                <div>{{date}}</div>
                <p v-html="content"></p>
            </div>
        </div>
        <div class="section">
            <div class="item">
                <label for="comment">
                    评论内容<span>*</span>
                </label>
                <span>
                    <textarea v-validate="'required'" name="comment"  id="comment" placeholder="请输入评论内容" cols="30" rows="4" v-model="comment"></textarea>
                    <i class="tip" v-show="errors.has('comment')">{{errors.first('comment')}}</i>
                </span>
            </div>
            <div class="item submintItem">
                <button class="button" @click="ok">评论</button>
            </div>
        </div>
        <div class="section secbefore">
            <div class="item jcly">
                <div></div>
                <div>精彩留言</div>
                <div></div>
            </div>
        </div>
        <div class="section">
            <div class="item leav-msg" v-for="(item,i) in list" :key="i">
                <div class="user">
                    <span class="left"><img :src="imgUrl+item.storeLogo"/><i>{{item.storeName}}</i></span>
                    <span class="time">{{item.createDate}}</span>
                </div>
                <div>{{item.content}}</div>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
    import { Validator } from 'vee-validate';
    import com from '../assets/common.js';
    import Exotic from '../config.js';
    export default{
        created(){
            let that=this;
            const dictionary = {
                zh_CN: {
                    messages: {
                        email: () => '邮箱格式不正确哦',
                        required:(field)=>field+"不能为空"
                    },
                    attributes:{
                        comment:'评论内容'
                    }
                }
            };
            Validator.localize(dictionary);
            if(this.$route.query.token!=undefined){
                this.token=this.$route.query.token;
            }
            if(this.$route.query.id!=undefined){
                this.id=parseInt(this.$route.query.id);
            }
            var param = {storePostingsId: that.id};
            that.MintUi.Indicator.open({
                text: '加载中...',
                spinnerType: 'fading-circle'
            })
            Exotic.ajaxData(that.token, Exotic.urlMap.other.topic, param, 'get', function(data){
                console.log(data)
                that.MintUi.Indicator.close()
                that.title=data.storePostings.title; //标题
                var createDate = com.getTimes(data.storePostings.createDate,'YY-MM-DD');
                that.date=createDate; //发布时间
                that.content=data.storePostings.content; //内容
                for(let i in data.commentList){
                    data.commentList[i].createDate=com.getTimes(data.commentList[i].createDate,'YY-MM-DD hh:mm');
                }
                that.list= com.reverseFn(data.commentList);
            });
        },
        data(){
            return {
                token:null,
                title:'',
                date:'',
                id:'',//ID
                //评论列表数据
                list:[],
                content:'',
                comment:'',//评论内容
                imgUrl:Exotic.imageUrl
            }
        },
        methods:{
            //弹出防止冒泡
            rt(){
                return true;
            },
            //运算找出对应ID的数据
            stext(arr,obj){
                for(let i=0;i<arr.length;i++){
                    if(arr[i].value==obj){
                        return arr[i];
                    }
                }
            },
            ok(){
                let that=this;
                if (that.comment == '') {
                    Exotic.tip({txt:"请填写评论内容"});
                    return false;
                }else{
                    var param = {
                        storePostingsId: that.id,
                        content: that.comment
                    };
                    console.log(param);
                    that.MintUi.Indicator.open({
                        text: '提交中...',
                        spinnerType: 'fading-circle'
                    })
                    Exotic.ajaxData(that.token,Exotic.urlMap.topic.add, param, 'post', function(data){
                        Exotic.tip({txt:"评论成功"});
                        that.MintUi.Indicator.close();
                        that.comment="";
                        var param = {storePostingsId:that.id};
                        Exotic.ajaxData(that.token,Exotic.urlMap.other.topic, param, 'get', function(data1){
                            for(let i in data1.commentList){
                                data1.commentList[i].createDate=com.getTimes(data1.commentList[i].createDate,'YY-MM-DD hh:mm');
                            }
                            that.list= com.reverseFn(data1.commentList);
                        });
                    });
                }
                
            }
        },
        mounted(){
          
        }
    }
</script>
<style lang="scss">
@import '../assets/index.scss';
.combox{
    overflow:hidden;
    display: flex;
    flex-direction: column;
}
.combox .pup{
    flex:1;
}
.combox .queding{
    flex:1;
}
.queding button{
    margin: 1.2rem auto;
    padding: .8rem 0;
    width: 80%;
    display: block;
    text-align: center;
    background-color: #39C073;
    color: #fff;
    border-radius: .5rem;
    outline: none;
    font-size: 1.2rem;
    border: 0;
}
.section .comment{
    padding:10px;
}
.section .comment h3,.section .comment p{
    margin:2px 0;
}
.section .jcly{
    display:flex;
    justify-content: center;
    align-items:center;
}
.section .jcly div:nth-child(2){
    width:8rem;
    text-align:center;
}
.section .jcly div:nth-child(1),
.section .jcly div:nth-child(3){
    height:1px;
    flex:1;
    background-color:#f1f1f1;
}
.secbefore .item{
    border:0;
}
.section .leav-msg{
    padding: 10px;
}
.section .leav-msg .user{
    display:flex;
    justify-content: space-between;
    height: 30px;
}
.section .leav-msg .user span.left{
    color:#555;
    font-size:14px;
    line-height:30px;
    float:left;
}
.section .leav-msg .user span img{
    width: 24px;
    padding:3px 0;
    height: 24px;
    display:block;
    float:left;
}
.section .leav-msg .user span i{
    line-height:30px;
    float:left;
    margin-left: 5px;
    overflow:hidden;
    white-space: nowrap;
    -ms-text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
}
.section .leav-msg div:nth-child(2){
    color:#999;
    font-size:16px;
}
.section .leav-msg .user .time{
    float:right;
    line-height:30px;
    text-align:right;
    font-size:14px;
    align-items: right;
}
</style>



