
const contains=function(arr,obj,attr) {
    if(attr){
        var i = arr.length;
        while (i--) {
            if ( arr[i][attr]=== obj) {
                return i;
            }
        }
        return "false";
    }else{
        var i = arr.length;
        while (i--) {
            if ( arr[i]=== obj) {
                return i;
            }
        }
        return "false";
    }
};
let getTimes=function(nS,type) {
    if(typeof nS == 'string'){
        var date=new Date(parseInt(nS));
    }else if(typeof nS == 'object'){
        var date=new Date(nS);
    }else{
        var date=new Date(nS);
    }
    var year=date.getFullYear();
    var mon = date.getMonth()+1;
    if(mon<10){
        mon="0"+mon;
    }
    var day = date.getDate();
    if(day<10){
        day="0"+day;
    }
    var hours = date.getHours();
    if(hours<10){
        hours="0"+hours;
    }
    var minu = date.getMinutes();
    if(minu<10){
        minu="0"+minu;
    }
    var sec = date.getSeconds();
    if(sec<10){
        sec="0"+sec;
    }
    if(type==''||type==undefined||type==null||type=="YY-MM-DD hh:mm:ss"){
        return year+'-'+mon+'-'+day+' '+hours+':'+minu+':'+sec;
    }else if(type=="YY-MM-DD"){
        return year+'-'+mon+'-'+day;
    }else if(type=="YY-MM-DD hh:mm"){
        return year+'-'+mon+'-'+day+' '+hours+':'+minu;
    }
}
let reverseFn=function(arr){
    let newArr=[];
    for(let item of arr){
        newArr.unshift(item);
    }
    return newArr;
}
let siblings=function(elem){ //参数elem就是想取谁的兄弟节点，就把那个元素传进去 
    var nodes=[]; //定义一个数组，用来存elem的兄弟元素 
    var previ=elem.previousSibling; 
    while(previ){ //先取o的哥哥们 判断有没有上一个哥哥元素，如果有则往下执行 previ表示previousSibling 
        if(previ.nodeType===1){ 
            nodes.push(previ); 
        } 
        previ=previ.previousSibling; //最后把上一个节点赋给previ 
    } 
    nodes.reverse(); //把顺序反转一下 这样元素的顺序就是按先后的了 
    var nexts=elem.nextSibling; //再取elem的弟弟 
    while(nexts){ //判断有没有下一个弟弟结点 nexts是nextSibling的意思 
        if(nexts.nodeType===1){ 
            nodes.push(nexts); 
        } 
        nexts=nexts.nextSibling; 
    } 
    return nodes; //最后按从老大到老小的顺序，把这一组元素返回 
} 
export default{
    contains,
    siblings,
    getTimes,
    reverseFn
}


